@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Montserrat', sans-serif;background-color:#eff1f6}

.App{min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;width:90%;padding:0;margin:0 auto}

.QuestionPage{height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}h2{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.go-back-arrow{text-decoration:none;display:inline-block;content:'';height:10px;width:10px;margin-right:12px;border-bottom:3px solid #0d61f2;border-right:3px solid #0d61f2;-webkit-transform:rotate(135deg);transform:rotate(135deg)}.question-title{color:#0d61f2;text-align:center}.video-container{height:85%;max-width:85%;-webkit-flex-basis:85%;flex-basis:85%}.options-container{padding:1em}.selection-option{padding:0.5em;width:50%;text-overflow:wrap;font-weight:500;font-size:1em;text-align:right}

h1{font-size:3em}.hero-row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:2em 0}.hero-keyword{color:#0d61f2}.hero-row-left{padding:1em}.hero-image{width:50%;margin-left:auto}@media screen and (max-width: 600px){.hero-row{-webkit-flex-direction:column-reverse;flex-direction:column-reverse}.hero-image{width:100%}.hero-row-left{padding-left:0;text-align:center}}

.accordion{border-radius:2px}.accordion__item+.accordion__item{margin:1em 0}.accordion__button{background-color:white;color:#0d61f2;font-weight:600;cursor:pointer;padding:18px;text-align:left;border:none;border:solid 1px #0d61f2}.accordion__button:hover{background-color:#0d61f2;color:white}.accordion__button[aria-expanded='true']{background-color:#0d61f2;color:white}.accordion__button:before{display:inline-block;content:'';height:10px;width:10px;margin-right:12px;border-bottom:2px solid currentColor;border-right:2px solid currentColor;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.accordion__button[aria-expanded='true']::before,.accordion__button[aria-selected='true']::before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}[hidden]{display:none}.accordion__panel{padding:1em;border:solid 0.5px black;-webkit-animation:fadein 0.5s ease-in-out;animation:fadein 0.5s ease-in-out}.accordion__panel:hover{background-color:#f2f4f8;font-weight:500}.question-link{color:#000000;text-decoration:none}@-webkit-keyframes fadein{0%{opacity:0}100%{opacity:1}}@keyframes fadein{0%{opacity:0}100%{opacity:1}}

.howworks-card{height:250px;width:250px;border:solid 1px #0d61f2;border-radius:1em;padding:1em;background-color:white}.howworks-img{height:150px}

.home-howworks{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-column-gap:1em;-webkit-column-gap:1em;column-gap:1em;grid-row-gap:1em;row-gap:1em;-webkit-justify-content:center;justify-content:center}

